import { useState, useEffect } from 'react';
import Img1 from '../../../images/ph-1.png';
import Img2 from '../../../images/ph-2.png';
import Img2Back from '../../../images/second-flip.png'
import Img3 from '../../../images/ph-3.png';
import Img4 from '../../../images/ph-4.png';
import Img5 from '../../../images/ph-5.png';
import Img5Back from '../../../images/fifth-flip.png'


function getWindowDimensions() {
  const inWidth = window.innerWidth;
  return inWidth;
}

function FrameProjects() {

    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

    useEffect(() => {
        function handleResize() {
          setWindowDimensions(getWindowDimensions());
        }
        
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
      }, []);

    
    const [scrollPos, setScrollPos] = useState(0);

    useEffect(()=> {
        window.addEventListener('scroll', () => setScrollPos(window.pageYOffset - document.getElementById('ph-gr-1').offsetTop + 440));
        return () => window.removeEventListener('scroll', setScrollPos);
    }, []);

    const [animateState, setAnimeteState] = useState(0);

    if(scrollPos > 0 && animateState === 0) {
        setAnimeteState(1);
    }

    const [activePhoto, setActivePhoto] = useState(0);

    if(windowDimensions < 500) {
        return (
            <div className='page-projects'>
                <div className='container' id='ph-gr-1'>
                    <span className='big-bold'>Наши проекты</span>
                    <div className='projects-group-one'  >
                        <div className={animateState === 1 ? 'projects-group-one__photo translate_0 animated' : 'projects-group-one__photo translate_0'} onClick={()=>setActivePhoto(1)}>
                            <img src={Img1}/>
                        </div>
                        <div className={animateState === 1 ? 'projects-group-one__photo translate_1 animated' : 'projects-group-one__photo translate_1'} style={activePhoto === 1 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} 
                        : activePhoto === 2 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 3 ? {transform:'translateY(0vw)',transition:'all 0.5s ease-in-out'} : activePhoto === 4 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 5 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : {}} onClick={()=>setActivePhoto(2)}>
                            <img src={Img2}/>
                        </div>
                        <div className={animateState === 1 ? 'projects-group-one__photo translate_2 animated' : 'projects-group-one__photo translate_2'} style={activePhoto === 1 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} 
                        : activePhoto === 2 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 3 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 4 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 5 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : {}} onClick={()=>setActivePhoto(3)}>
                            <img src={Img3}/>
                        </div>
                        <div className={animateState === 1 ? 'projects-group-one__photo translate_3 animated' : 'projects-group-one__photo translate_3'} style={activePhoto === 1 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} 
                        : activePhoto === 2 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 3 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 4 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 5 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : {}} onClick={()=>setActivePhoto(4)}>
                            <img src={Img5}/>
                        </div>
                        <div className={animateState === 1 ? 'projects-group-one__photo translate_4 animated' : 'projects-group-one__photo translate_4'} style={activePhoto === 1 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} 
                        : activePhoto === 2 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 3 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 4 ? {transform:'translateY(46vw)', transition:'all 0.5s ease-in-out'} : activePhoto === 5 ? {transform:'translateY(0vw)', transition:'all 0.5s ease-in-out'} : {}} onClick={()=>setActivePhoto(5)}>
                            <img src={Img4}/>
                        </div>
                    </div>
                </div> 
                
            </div>
        );
    }
    else {
        return (
            <div className='page-projects'>
                <div className='container' id='ph-gr-1'>
                    <span className='big-bold'>Наши проекты</span>
                    <div className='projects-group-one'>
                        <div className='projects-group-one__photo'>
                            <img src={Img1}/>
                        </div>
                        <div className='projects-group-one__photo two-pc'>
                            <div className='flip-group'>
                                <img className='ph-top' src={Img2}/>
                                <img className='ph-back' src={Img2Back}/>
                            </div>
                        </div>
                        <div className='projects-group-one__photo'>
                            <img src={Img3}/>
                        </div>
                    </div>
                    <div className='projects-group-two'>
                        <div className='projects-group-two__photo'>
                            <img src={Img5}/>
                        </div>
                        <div className='projects-group-two__photo two-pc'>
                            <div className='flip-group'>
                                <img className='ph-top' src={Img4}/>
                                <img className='ph-back' src={Img5Back}/>
                            </div>

                        </div>
                    </div>
                </div>
                
            </div>
        );
    }


}

export default FrameProjects;
