import marketerImg from '../../../images/marketer-photo.png';
import messy from '../../../images/messy.svg';




function Frame01({funct, secFunct}) {

    return (
        <div className='page-01'>
            <div className='page-01__navbar'>
                <div className='container'>
                    <div className='small-regular'>
                        <span className='small-regular' style={{fontWeight:'700', display:'block'}}>Студия 12.7</span>
                        <span className='small-regular'>дизайн, маркетинг, разработка</span>
                    </div>
                    <div className='navbar__contact small-regular' onClick={funct}>Связаться</div>
                </div>
            </div>
            <div className='page-01__header-container'>
                <div className='container'>
                    <span className='page-01__header big-bold'>Увеличим продажи в вашем бизнесе</span>
                    <span className='page-01__text medium-regular'> Благодаря объединению маркетинговых инструментов</span>
                </div>

            </div>

            <div className='page-01__main-content'>
                <div className='container'>
                    <div className='main-content__left-side'>
                        <div className='left-side__border-group'>
                            <span className='left-side__text medium-regular'>Ответьте на 5 вопросов и получите решение для увеличения продаж</span>
                            <div className='left-side__button medium-regular' onClick={secFunct}>Получить решение</div>
                            <img src={marketerImg}/>
                            <div className='about-marketer'>
                                <span className='small-regular'><span className='circle'>.</span> Ильсаф Яруллин <br/> Старший интернет маркетолог</span>
                            </div>
                            
                        </div>
                        <span className='small-regular'>ГОСТ 12.7</span>
                        <div className='mobile-button' onClick={secFunct}>Получить решение</div>
                        {/* <a href='#formr2' className='mobile-button'>Получить решение</a> */}
                    </div>
                    <div className='main-content__right-side'>
                        <img src={messy}/>
                    </div>
                </div>


            </div>
        </div>
    );
}

export default Frame01;
