import './App.scss';
import { useRef, useEffect } from 'react';

import Frame01 from './components/frame01';
import Frame02 from './components/frame02';
import Frame03 from './components/frame03';
import Frame04 from './components/frame04';
import Frame05 from './components/frame05';

import FrameProjects from './components/frameProjects'

function App() {
    const formEl = useRef();
    const onButtonClick = () => {
        // `current` указывает на смонтированный элемент `input`
        formEl.current.focus();
    };
    const formEl2 = useRef();
    const onButtonClick2 = () => {
        // `current` указывает на смонтированный элемент `input`
        formEl2.current.focus();
    };
    useEffect(() => {
        document.title = 'Студия 12.7';
    });
    return (
        <div className="App">
            <Frame01 funct={onButtonClick} secFunct={onButtonClick2}/>
            <FrameProjects/>
            <Frame02/>
            <Frame03 refr2={formEl2}/>
            <Frame04/>
            <Frame05 refr={formEl}/>
        </div>
    );
}

export default App;
