import Quiz from './components/quiz';

function Frame03({refr2}) {
    return (
        <div className='page-03'>
            <div className='container'>
                <span className='page-03__header big-bold'>Решение для вашего бизнеса</span>
                <div className='page-03__content'>
                    <input type='text' style={{position:'absolute', opacity:'0'}} ref={refr2}></input>
                    <div className='content__quiz-information'>
                        <span className='medium-regular'>Вам нужно ответить на 5 вопрос и я подготовлю способы для увеличения продаж. Это бесплатно!</span>
                        <span className='small-regular about-marketer'><span className='circle'>.</span>Ильсаф Яруллин</span>
                        <span className='small-regular'>Старший интернет маркетолог</span>
                    </div>
                    <Quiz/>
                </div>
            </div>

        </div>
    );
}

export default Frame03;