import { useState } from "react"
import backend from '../../../../../../../backend/quiz.php';

function Question( {props, changer} ) {

    const [numbQuestion, setNumbQuestion] = useState(0);
    const[errPhone, throwErrPhone] = useState(0);
    const[checkedState, setChekedState] = useState('');



    async function SendMail(variables) {

        if(!phoneTest(variables[variables.length-1].answeredVariant)) {
            
            let formData = {
                title_0: variables[0].title,
                answer_0: variables[0].answeredVariant,
                title_1: variables[1].title,
                answer_1: variables[1].answeredVariant,
                title_2: variables[2].title,
                answer_2: variables[2].answeredVariant,
                title_3: variables[3].title,
                answer_3: variables[3].answeredVariant,
                title_4: variables[4].title,
                answer_4: variables[4].answeredVariant,
                title_5: variables[5].title,
                answer_5: variables[5].answeredVariant,
            }

            let strFormData = JSON.stringify(formData);

            let response = await fetch(backend, {
                method: 'POST', 
                body: strFormData
            });
            if(response.ok) {
                // console.log('sended')
                setNumbQuestion(999);
                window.ym(86365877,'reachGoal','lead');
                window.fbq('track', 'Lead');
            }
            else {
                // console.log('serverErr')
                setNumbQuestion(999);
                // console.log(strFormData)
            }
        }
        else {
            throwErrPhone(1);
        }
        
    }

    function phoneTest(input) {
        return !/^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/.test(input);
    }


    return (
        <div className='question'>
            <span className='question-title medium-regular'>{numbQuestion === 999 ? '' : errPhone === 0 ? props[numbQuestion].title : 'Неправильный формат телефона'}</span>
            <div className='answer-wrapper'>
                {numbQuestion === 999 ? <div><span className='medium-regular'>Спасибо, мы свяжемся с вами в ближайшее время!</span></div> : props[numbQuestion].variants.length < 2 ? props[numbQuestion].variants.map((pool) =><label key={numbQuestion}><input className='small-regular' type='text' placeholder={pool} onChange={(event) => {
                    changer(event.target.value, numbQuestion);
                }} /></label> ) : props[numbQuestion].variants.map((pool, index) => {
                    return (
                        <label className='answer-question small-regular'>
                            <input onChange={(event)=> {
                                changer(event.target.value, numbQuestion);
                                setChekedState(JSON.stringify(numbQuestion) + JSON.stringify(index));
                                }} key={JSON.stringify(numbQuestion) + JSON.stringify(index)} checked={checkedState === JSON.stringify(numbQuestion) + JSON.stringify(index) ? true: false} value={pool} type='radio'> 
                            </input>
                            <span className='checkmark'>{pool}</span>
                        </label>
                    );
                    }
                )}
            </div>
            <div className='next-question small-regular' style={numbQuestion === 999 ? {display:'none'} : {display:'flex'}} onClick={()=> {
                if(numbQuestion === props.length - 1) {
                    SendMail(props);
                }
                else {
                    setNumbQuestion(numbQuestion + 1);
                }
                
            }}>
                {numbQuestion === 5 ? 'Отправить' : 'Следующий вопрос'}
            </div>
       </div> 
    )
}

export default Question;