import Question from "./components/question"

function Quiz() {

    const allQuestions = [
        {
            title: 'В какой сфере вы работаете?',
            variants: [
                '1. Производите товар',
                '2. Продаете услугу',
                '3. Занимаетесь торговлей',
                '4. Продаете инфопродукт',
                '5. Другое'
            ],
            answeredVariant: ''
        },
        {
            title: 'Укажите сферу:',
            variants: [
                'К примеру: ремонт квартир, строительство домой'
            ],
            answeredVariant: ''
        },
        {
            title: 'Чего вы ожидаете от наших услуг?',
            variants: [
                '1. Увеличение клиентов вашего продукта',
                '2. Повышение узнаваемости',
                '3. Пока не знаю'
            ],
            answeredVariant: ''
        },
        {
            title: 'Какие у вас рекламные бюджеты?',
            variants: [
                'До 200 000 рублей',
                'От 200 000 до 600 000 рублей',
                'От 600 000 до 1 500 000 рублей',
                'От 1 500 000 до 5 000 000 рублей',
                'Свыше 5 000 000 рублей'
            ],
            answeredVariant: ''
        },
        {
            title: 'В какой срок вам нужна услуга?',
            variants: [
                'До 5 дней',
                'От 5 до 14 дней',
                'От 14 до 30 дней',
                'От 30 дней'
            ],
            answeredVariant: ''
        },
        {
            title: 'Введите номер телефона',
            variants: [
                ''
            ],
            answeredVariant: ''
        }
    ]

    function handleChange(event, numberQuestion) {
        allQuestions[numberQuestion].answeredVariant = event;
    }

    return (
        <div className='content__quiz'>
            <Question  props={allQuestions} changer={handleChange} />
        </div>
    );
}

export default Quiz;