import { useRef, useState } from "react";
import backend from '../../../backend/form.php';


function Frame05({refr}) {

    const [checkedState, setChecked] = useState(0);
    const [dataName, setName] = useState('');
    const [dataPhone, setPhone] = useState('');
    const [dataBank, setBank] = useState('до 1 000$');

    const[errPhone, throwErrPhone] = useState(0);

    const[thxState, setThxState] = useState(0);

    let formData = {
        name:dataName,
        phone:dataPhone,
        bank:dataBank
    }

    async function SendMail(event) {
        event.preventDefault();
        //создать условия для валидации введённых данных
        if(!phoneTest(dataPhone)) {
            console.log(formData);
            //логику отправки на сенд пхп
            let strFormData = JSON.stringify(formData);
            let response = await fetch(backend, {
                method: 'POST', 
                body: strFormData
            });
            if(response.ok) {
                setThxState(1);
                window.ym(86365877,'reachGoal','lead');
                window.fbq('track', 'Lead');
            }
            else {
                setThxState(1);
            }
        }
        else {
            throwErrPhone(1);
        }
        
    }

    function phoneTest(input) {
        return !/^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/.test(input);
    }

    return (
        <div className='page-05'>
            <div className='container'>
                <span className='page-05__header big-bold'>Начнём проект вместе?</span>
            </div>

            <span className='question-img'>?</span>
            <div className='container'>
                <div className='page-05__form'>
                    <form action="" method="post" onSubmit={SendMail} style={thxState === 0 ? {display:'static'} :{display:'none'}}>
                        <label className='dp-block'>
                            <span className='medium-regular'>ваше имя</span>
                            <input ref={refr} id="POST-name" type="text" name="name" className='dp-block rectangle small-regular' onChange={(event)=> setName(event.target.value)}/>
                        </label>
                        <label className='dp-block'>
                            <span className='medium-regular'>{errPhone === 0 ? 'ваш телефон' : 'неправильный формат телефона'}</span>
                            <input id="POST-phone" type="text" name="phone" className='dp-block rectangle small-regular' onChange={(event)=> setPhone(event.target.value)}/>
                        </label>
                        <span className='dp-block medium-regular'>ваш бюджет</span>
                        <div className='radio-btns-container'>
                            <label className='radio-btn'>
                                <input type="radio" name="bank" value='до 1 000$' checked={checkedState === 0 ? true: false} onChange={(event)=> {
                                    setChecked(0);
                                    setBank(event.target.value);
                                    }} /> 
                                <span className='checkmark'> до 1 000$</span>
                            </label>
                            <label className='radio-btn'>
                                <input type="radio" name="bank" value='от 1 000$ до 5 000$' checked={checkedState === 1 ? true: false} onChange={(event)=> {
                                    setChecked(1);
                                    setBank(event.target.value);
                                    }}/> 
                                <span className='checkmark'> от 1 000$ <br/> до 5 000$</span>
                            </label>
                            <label className='radio-btn'>
                                <input type="radio" name="bank" value='от 5 000$' checked={checkedState === 2 ? true: false} onChange={(event)=> {
                                    setChecked(2);
                                    setBank(event.target.value);
                                    }}/> 
                                <span className='checkmark'> от 5 000$</span>
                            </label>
                        </div>
                        <input type="submit" value="Оставить заявку" className='dp-block rectangle submit-btn small-regular'/>
                    </form>
                    <div style={thxState === 1 ? {display:'flex', height:'30vw'} : {display:'none'}}><span className='medium-regular'>Спасибо за заявку, мы свяжемся с вами в ближайшее время!</span></div>
                </div>
            </div>
            <div className='page-05__footer'>
                <div className='container'>
                    <span className='small-regular'>+7 (499) 99 127 99</span>
                    <span className='small-regular'>studio@12-7working-out.ru</span>
                </div>
            </div>
        </div>
    );
}

export default Frame05;